import Detail from "../../public/detail.vue"
import Head from "../../public/head.vue"
import None from '../../public/noNumber'
export default {
  name: "wishPage",
  data(){
    return{
      api: this.GLOBAL.baseURL,
      activeList:[],
      group_id:'',
      Groupname:'',
      isDeel:false,
      isXin:false
    }
  },
  components:{
    Head,
    Detail,
    None
  },
  methods:{
    //移除
    deel(item,index){
      this.$confirm('此操作将移除您的心愿单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deelGroud(item,index)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    deelGroud(item,index){
      this.$http.post(this.api + '/home/Comment/collection',{
        token: localStorage.getItem('token'),
        table_id: item.table_id,
        group_id:this.group_id,
        flag: item.flag,
        type: 2
      })
        .then(res=>{
          if(res.data.code == 1){
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.activeList.splice(index,1)
          }else if(res.data.code == 3){

          }else{
            alert(res.data.msg)
          }
        })
    },
    toPublish(item,index){
      this.$router.push({
        path: '/publishPage',
        query:{
          information:item.table_id
        }
      })
    },
    getGroup_id(){
      this.group_id = this.$route.query.group_id
      this.getCollect()
    },
    getCollect(){
      this.$http.post(this.api + '/collectional',{
        token: localStorage.getItem('token'),
        group_id: this.group_id
      })
        .then(res=>{
          if(res.data.code == 1){
            let data = res.data.data.data
            this.activeList = data
            if(!data.length){
              this.isXin = true
            }else{
              this.isXin = false
            }
          }else if(res.data.code == 3){
            alert(res.data.msg)
          }else{
            alert(res.data.msg)
          }
        })
        .catch(error=>{
          alert(error)
        })
    },
    toStory(item){
      this.$router.push({
        path: '/storyPage',
        query:{
          information: item.table_id
        }
      })
    }
  },
  mounted() {
    let _this = this
    _this.getGroup_id()
    _this.Groupname = this.$route.query.name
  }
}



